import React, { Component, createContext } from "react";
import axios from "axios";
import {baseUrl} from "./constants";
import {userToJson} from "./models/user_model";

export const globalC = createContext();

export class AppContext extends Component {
    state = {
        authLogin: null,
        authToken: null,
        authLoginError: null
    };
    componentDidMount() {
        // let localData = JSON.parse(localStorage.getItem("loginDetail"));
        console.log(this.state.authLoginError);
        let localData = localStorage.getItem('user');
        let token = localStorage.getItem('token');
    }

    loginData = async (payload) => {
        await axios.post(`${baseUrl}users/login`, payload)
            .then((res) => {
                console.log(res.status);
                if (res.status === 200) {
                    localStorage.setItem("token", res.data.token);
                    let user = userToJson(JSON.stringify(res.data.data));
                    localStorage.setItem("user", user);
                    // localStorage.setItem("loginDetail", JSON.stringify(res.data));
                }
                else {
                    console.log("Login Failed");
                    this.setState({
                        authLoginError: "Invalid user if or password"
                    })
                }
            })
            .catch((err) =>
                this.setState({
                    authLoginError: err
                })
            );
    };

    render() {
        return (
            <globalC.Provider
                value={{
                    ...this.state,
                    loginData: this.loginData,
                    authLogin: this.state.authLogin,
                    authToken: this.state.authToken,
                    authLoginError: this.state.authLoginError
                }}
            >
                {this.props.children}
            </globalC.Provider>
        );
    }
}
