import React, { useState, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import { db } from '../../../firebase-config'
import 'react-toastify/dist/ReactToastify.css';
import '../../assets/main.css'
import axios from "axios";
import {baseUrl} from "../../../constants";
import {toast} from "react-toastify";

export default function EditDepartment() {
  const [departID, setDepartId] = useState('');
  const [partname, setPartname] = useState('');
  const [partmanage, setPartmanage] = useState('');
  const [budget, setBudget] = useState('');
  const [description, setDescription] = useState('');
  const [superpartname, setSuperPartname] = useState('');
  const [selectmanager, setSelectManager] = useState([]);
  const [depart, setDepart] = useState([]);
  const [accounts, setAccounts] = useState([]);
  const [selectedAccount, setSelectedAccount] = useState('');
  const editID = useParams().id
  const authID = sessionStorage.getItem('UID')
  let navigate = useNavigate();
  useEffect(async () =>{

    let id = editID;

    const token = localStorage.getItem('token');
    const headers = {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${token}`
    };

    await axios.get(`${baseUrl}users/editDepart/${id}`, {headers})
        .then((res) => {
          console.log(res.data.data[0]);
          setDepartId(res.data.data[0].id);
          setPartname(res.data.data[0].depart_name)
          setPartmanage(res.data.data[0].manager_id)
          setSuperPartname(res.data.data[0].parent_depart)
          setBudget(res.data.data[0].budget)
          setDescription(res.data.data[0].description)
          setSelectedAccount(res.data.data[0].account_id);
        });

    await axios.get(`${baseUrl}users/getCompanyManagementUsers/${localStorage.getItem("companyID")}`, {headers})
        .then((res) => {

          let arr = [];
          for(let i = 0; i<res.data.data.length; i++) {
            console.log(res.data.data[i].first_name + " " + res.data.data[i].last_name);
            arr.push({"id": res.data.data[i].id, "name": res.data.data[i].first_name + " " + res.data.data[i].last_name});
          }
          setSelectManager(arr)
        });

    await axios.get(`${baseUrl}users/getParentDeparts`, {headers})
        .then((res) => {
          let arr = [];
          for(let i = 0; i<res.data.data.length; i++) {
            console.log(res.data.data[i]);
            arr.push(res.data.data[i]);
          }
          setDepart(arr)
        });

    await axios.get(`${baseUrl}users/getCompanyAccount/`+localStorage.getItem("UID"), {headers})
        .then((res) => {
          let superarr = [];
          for (let i = 0; i < res.data.data.length; i++) {
            const account_name = res.data.data[i].name;
            const id = res.data.data[i].id;
            const type = res.data.data[i].type;
            const code = res.data.data[i].code;
            const company_id = res.data.data[i].company_id;
            const accountID = res.data.data[i].accountID;
            superarr.push({"id": id, "name": account_name, "type": type, "code": code, "company_id": company_id, "accountID": accountID})
          }
          setAccounts(superarr);
        });

    //
    // await axios.get(`${baseUrl}users/getDeparts/${localStorage.getItem("companyID")}`,headers)
    //     .then((res) => {
    //       console.log(res.data.data);
    //       var superarr = [];
    //       for(let i = 0; i < res.data.data.length; i++){
    //         const departdata = res.data.data[i].depart_name
    //         const departid = res.data.data[i].id
    //         superarr.push({"id":departid,"name":departdata})
    //       }
    //       setDepart(superarr)
    //     });

  },[])
  function onCancel (e) {
    e.preventDefault();
    navigate('/department');
  }
  const handlepartName =(e) =>{
    setPartname(e.target.value);
  }
  const handleBudget =(e) =>{
    setBudget(e.target.value);
  }
  const handlepartmanage = (e) => {
    setPartmanage(e.target.value);
  }
  const handlesuperpart = (e) => {
    setSuperPartname(e.target.value);
  }
  const handleDescription =(e) =>{
    setDescription(e.target.value);
  }

  async function updateDepart(parent_id, depart_name, budget,manager_id, selectedAccount,description) {
    const token = localStorage.getItem('token');
    const headers = {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${token}`
    };

    const body = {
      parent_id: parent_id,
      depart_name: depart_name,
      budget: budget,
      manager_id: manager_id,
      description:description,
      id: departID,
      account_id:selectedAccount
    };
    console.log(body);
    let response = await axios.post(`${baseUrl}users/updateDepart`, body,{headers})
    toast.success(response.data.message);
    // alert(response.data.message);
    // console.log(response);
    navigate('/department');
    // alert(response.data.message);
    // navigate('/');
  }
  const handleAccount = (e) => {
    setSelectedAccount(e.target.value);
  }

  async function handleSubmit(e) {
    e.preventDefault();

    await updateDepart(superpartname, partname, budget,partmanage , selectedAccount,description);

  }
  return(
      <div className="bg-blue-50 flex justify-center items-center px-5 py-5" style={{"height": "90vh"}}>
        <div className="container max-w-3xl mx-auto bg-white rounded-xl shadow-xl overflow-hidden sm:max-w-xl pt-3 pb-3  flex-1 flex flex-col items-center justify-center">
          <div className="px-2 py-1">
        <div className="uppercase text-5xl textstylecolor font-Medium text-center mb-3 italic">Edit Department</div>
            <hr/>
          <form className="w-full max-w-lg object-center" onSubmit={handleSubmit}>
            <div className="flex flex-wrap -mx-3">
            <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                    Department
                  </label>
                </div>
              <select
                  className={"appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 "}
                  value={superpartname}
                  onChange={handlesuperpart}
                  style={{border:"1px solid #f0f0f0"}}
              >
                <option selected>Choose Department</option>
                {depart.length > 0 ? (
                    depart.map((part, id) => (
                        <option key={id} value={part.id}>{part.name}</option>
                    ))
                ) : (
                    <option>Select Department</option>
                )
                }
              </select>
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                    Sub Department
                  </label>
                </div>
                <input 
                  type="text"
                  className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  value={partname}
                  onChange={handlepartName}
                />
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                    Budget
                  </label>
                </div>
                <input 
                  type="text"
                  className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  value={budget}
                  onChange={handleBudget}
                />
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                    department manager
                  </label>
                </div>
                <select
                    className={"appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 "}
                    value={partmanage}
                    onChange={handlepartmanage}
                    style={{border:"1px solid #f0f0f0"}}
                >
                  <option selected>Select Manager</option>
                  {selectmanager.length > 0 ? (
                      selectmanager.map((part ,id) => (
                          <option key={id} value={part.id}>{part.name}</option>
                      ))
                  ) : (
                      <option>No Management User Found</option>
                  )
                  }
                </select>
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">
                    Description
                  </label>
                </div>
                <textarea
                    className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                    rows="5"
                    minLength="20"
                    maxLength="1000"
                    size="150"
                    placeholder="Write about department..."
                    value={description}
                    onChange={handleDescription}
                />
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                    Accounts
                  </label>
                  {/*<p className={"inputcolor text-xs italic ml-1 mt-3 " + (companyvalid ? "visible" : "invisible")}>Company required</p>*/}
                </div>
                <select
                    className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                    value={selectedAccount}
                    onChange={handleAccount}
                >
                  <option selected>Select Account</option>
                  {accounts.length > 0 ? (
                      accounts.map((part ,id) => (
                          <option key={id} value={part.id}>({part.code!=null?part.code:part.accountID}) {part.name} </option>
                      ))
                  ) : (
                      <option>No Account Found</option>
                  )
                  }
                </select>
              </div>
            </div>

            <div className="flex flex-wrap -mx-3 mt-3">
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button type="submit" className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight">
                  <span className="mr-2 uppercase">SAVE</span>
                </button>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight" onClick={onCancel}>
                  <span className="mr-2 uppercase">Cancel</span>
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}