import React, {useEffect, useState} from 'react'
import '../../Components/assets/main.css'
import axios from "axios";
import {baseUrl} from "../../constants";
export default function UserDashboard() {
    const [companyName, setCompanyName] = useState([]);

    useEffect(async () => {
        try {
            const token = localStorage.getItem('token');
            const headers = {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            };
            await axios.get(`${baseUrl}users/getCompanyByID/${localStorage.getItem("companyID")}`, {headers})
                .then((res) => {
                    console.log(res.data.data[0].company_name);
                    setCompanyName(res.data.data[0].company_name);
                });
        }
        catch (e) {

        }

    });
  return(
    <div className="min-w-screen min-h-screen flex justify-center px-5 py-5">
      <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-6">
        <div className="text-5xl leading-5 text-gray-500 text-center font-bold uppercase mb-5">company:{ companyName }</div>
        <h1 className="headcolor text-6xl italic fot-bold">User Graphic visual</h1>
      </div>
    </div>
  )
}