import React, {useEffect, useState} from "react";
import '../../Components/assets/main.css'
import axios from "axios";
import {baseUrl} from "../../constants";
export default function AdminDashboard() {
    const [company_count, setCompanyCount] = useState("");
    const [depart_count, setDepartCount] = useState("");
    const [user_count, setUserCount] = useState("");
    const [account_count, setAccountCount] = useState("");
    const [expense_count, setExpenseCount] = useState("");
    useEffect(async () => {
        // console.log("ID", edituserID);
        const token = localStorage.getItem('token');
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
        };

        await axios.get(`${baseUrl}users/getCounts/${localStorage.getItem("UID")}/${localStorage.getItem("companyID")}`, {headers})
            .then((res) => {
                console.log("count",res.data.company_count[0].count);
                setCompanyCount(res.data.company_count[0].count);
                setDepartCount(res.data.depart_count[0].count);
                setUserCount(res.data.user_count[0].count);
                setAccountCount(res.data.account_count[0].count);
                setExpenseCount(res.data.expense_count[0].count);
            });

    });
  return(
      <div>
          <div className={"justify-b"}>
              <div className={"p-3 m-5 rounded-lg shadow-md text-center"} style={{backgroundImage: "linear-gradient(120deg, #1a2999, #1a2956)",color:"white"}}>
                  <h1 style={{fontSize:"20px",fontWeight:"500"}} className={"uppercase"}>Total Companies</h1>
                  <h2 style={{fontSize:"40px",fontWeight:"bold"}}>{company_count}</h2>
              </div>
              <div className={"p-3 m-5 rounded-lg shadow-md text-center"} style={{backgroundImage: "linear-gradient(120deg, #1a2999, #1a2956)",color:"white"}}>
                  <h1 style={{fontSize:"20px",fontWeight:"500"}} className={"uppercase"}>Total Departments</h1>
                  <h2 style={{fontSize:"40px",fontWeight:"bold"}}>{depart_count}</h2>
              </div>
              <div className={"p-3 m-5 rounded-lg shadow-md text-center"} style={{backgroundImage: "linear-gradient(120deg, #1a2999, #1a2956)",color:"white"}}>
                  <h1 style={{fontSize:"20px",fontWeight:"500"}} className={"uppercase"}>Total Users</h1>
                  <h2 style={{fontSize:"40px",fontWeight:"bold"}}>{user_count}</h2>
              </div>
              <div className={"p-3 m-5 rounded-lg shadow-md text-center"} style={{backgroundImage: "linear-gradient(120deg, #1a2999, #1a2956)",color:"white"}}>
                  <h1 style={{fontSize:"20px",fontWeight:"500"}} className={"uppercase"}>Total Accounts</h1>
                  <h2 style={{fontSize:"40px",fontWeight:"bold"}}>{account_count}</h2>
              </div>
              <div className={"p-3 m-5 rounded-lg shadow-md text-center"} style={{backgroundImage: "linear-gradient(120deg, #1a2999, #1a2956)",color:"white"}}>
                  <h1 style={{fontSize:"20px",fontWeight:"500"}} className={"uppercase"}>Total Expenses</h1>
                  <h2 style={{fontSize:"40px",fontWeight:"bold"}}>${parseFloat(expense_count).toFixed(2)}</h2>
              </div>
          </div>

      </div>
    // <div className="min-w-screen min-h-screen flex justify-center px-5 py-5">
    //   <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-6">
    //     <h1 className="headcolor text-6xl italic fot-bold">Admin Graphic visual</h1>
    //   </div>
    // </div>
  )
}