import React, {useEffect, useState} from 'react'
import {Link, useParams, useNavigate} from "react-router-dom"
import '../assets/Login.css'
import { db, auth } from '../../firebase-config'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from "axios";
import {baseUrl} from "../../constants";
export default function Reset () {
    let navigate = useNavigate();
    const [password, setPassword] = useState('');
    const [retypepassword, setRetypePassword] = useState('');
    const [passwordupdated, setPasswordUpdated] = useState(false);
    const [notMatch, setNotMatch] = useState(false);
    const [body, setBody] = useState(false);
    // const [linkexpire, setLinkExpire] = useState(false);
    const email = useParams().email;
    const token = useParams().token;
    const handlePassword = (e) => {
        setPassword(e.target.value);
    };
    const handleConfirmPassword = (e) => {
        setRetypePassword(e.target.value);
    };

    useEffect(async () => {
        if(passwordupdated == false) {
            await axios.get(`${baseUrl}users/checkPasswordResetToken/${token}`)
                .then( async (res) => {
                    console.log(res.data.data[0].count);
                    if(res.data.data[0].count===1) {
                        setBody(true);
                    }
                    else {
                        setBody(false);
                        toast.error("Link Expired");
                        alert("Link Expired");
                        navigate('/');
                    }
                });
        }

    });

    async function handleSubmit(e) {
        e.preventDefault();
        if ( password !== retypepassword) {
            setNotMatch(true);
            return
        }
        else {
            setNotMatch(false);
            setPasswordUpdated(true);
            const body = {
                email: email,
                password: password
            };
            console.log(body);
            let response = await axios.post(`${baseUrl}users/updatePassword`, body);
            toast.success(response.data.message);
            // navigate('/');
        }

        // db.collection("Users").where("useremail", "==", email )
        // .get()
        // .then(doc => {
        //   if( doc.docs.length === 0 ) {
        //     toast.warn("Email doesn't exist.")
        //    return
        //   } else{
        //     auth.sendPasswordResetEmail(email)
        //     .then(() =>{
        //       toast.info("Password reset email sent!")
        //     }).catch(error =>{
        //       console.log(error)
        //     })
        //   }
        // })
    }
    return (
        <div className="bg-grey-lighter min-h-screen flex flex-col app">
            <ToastContainer />
            <div className="container max-w-lg mx-auto flex-1 flex flex-col items-center justify-center px-2">
                <div className={"bg-white px-8 py-4 rounded shadow-md text-black w-full "+ (body ? "d-block" : "d-none")}>
                    <div className="mb-6 flex justify-center">
                        <img
                            alt="logo"
                            className="object-between w-100 h-8"
                            src="https://wepull.netlify.app/logo.png"
                        />
                    </div>
                    <hr />
                    <label
                        className="mb-1 block text-2xl font-sans text-center text-black">
                        Password Reset
                    </label>
                    <form onSubmit={handleSubmit}>
                        <label
                            className="mb-2 block text-xm text-gray-600">
                            Password
                        </label>
                        <input
                            type="password"
                            className={"block w-full p-1 pl-3 rounded border border-gray-light"}
                            placeholder="Enter Password"
                            value={password}
                            required={true}
                            onChange={handlePassword}
                        />
                        <br/>
                        <label
                            className="mb-2 block text-xm text-gray-600">
                            Confirm Password
                        </label>
                        <input
                            type="password"
                            className={"block w-full p-1 pl-3 rounded border border-gray-light"}
                            placeholder="Confirm Password"
                            value={retypepassword}
                            required={true}
                            onChange={handleConfirmPassword}
                        />
                        <p className={"inputcolor text-xm text-center italic mb-1 mt-1 ml-1 " + (notMatch ? "d-block" : "d-none")}>password do not match</p>
                        <p className={"successcolor text-xm text-center italic mb-1 mt-1 ml-1 " + (passwordupdated ? "d-block" : "d-none")}>password updated successfully, go back to <Link className="font-medium text-blue-900 hover:text-xl"  to="/">
                            Login
                        </Link></p>
                        <br/>
                        <button
                            type="submit"
                            className="mb-2 flex justify-center py-2 px-4 text-md w-full font-medium rounded-md text-white bg-blue-900 hover:bg-blue-800"
                        >
                            Reset
                        </button>
                    </form>
                    <div className="text-grey-dark mt-1  flex justify-center">
                        Don't have an account? &nbsp;&nbsp;
                        <Link className="font-medium text-blue-900 hover:text-xl"  to="/signup">
                            Create Account
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}
