import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom';
import { useParams } from "react-router-dom";
import { db } from '../../../firebase-config'
import 'react-toastify/dist/ReactToastify.css';
import '../../assets/main.css'
import axios from "axios";
import {baseUrl} from "../../../constants";
import {toast} from "react-toastify";
export default function EditUser() {
  const [userid, setUserId] = useState("");
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [superpartname, setSuperPartname] = useState('');
  const [role, setRole] = useState("");
  const [userrole, setuserRole] = useState("");
  const [userDepart, setuserDepart] = useState("");
  const [phone, setPhone] = useState("");
  let navigate = useNavigate();
  const [depart, setDepart] = useState([]);
  const edituserID = useParams().id
  function cancelButton(e) {
    e.preventDefault();
    navigate('/user');
  }
  useEffect(async () => {
    console.log("ID",edituserID);
    const token = localStorage.getItem('token');
    const headers = {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${token}`
    };

    await axios.get(`${baseUrl}users/getDeparts/${localStorage.getItem("companyID")}`, {headers})
        .then((res) => {
          console.log(res.data.data);
          var superarr = [];
          for(let i = 0; i < res.data.data.length; i++){
            const departdata = res.data.data[i].depart_name
            const departid = res.data.data[i].id
            superarr.push({"id":departid,"name":departdata})
          }
          setDepart(superarr)
        });


    await axios.get(`${baseUrl}users/editUser/${edituserID}`, {headers})
        .then((res) => {
          console.log(res.data.data[0]);
          setUserId(res.data.data[0].id);
          setFirstname(res.data.data[0].first_name);
          setLastname(res.data.data[0].last_name);
          setPhone(res.data.data[0].contact);
          setuserRole(res.data.data[0].role_id);
          setuserDepart(res.data.data[0].depart_id);
          setRole(res.data.data[0].role_id);
          setSuperPartname(res.data.data[0].depart_id)
        });
    console.log("Role",userrole);
  }, [])

  const handlefirstName = (e) => {
    setFirstname(e.target.value)
  };
  const handlelastName = (e) => {
    setLastname(e.target.value);
  };
  const handlephone = (e) => {
    setPhone(e.target.value);
  };
  const handleRole = (e) => {
    setRole(e.target.value)
    // setRoleValid(false);
    console.log(e.target.value)
  }
  // const handlesuperpart = (e) => {
  //   setSuperPartname(e.target.value);
  // }
  const handleCheck = (e) => {
    const value = e.target.value
    // var arr = superpartname;
    // arr.push(value);
    setSuperPartname(e.target.value)
    // console.log(arr)
  }

  async function updateUser(fname, lname,phone ,depart_id, role_id) {
    try{
      const token = localStorage.getItem('token');
      const headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      };

      const body = {
        user_id: userid,
        first_name: fname,
        last_name: lname,
        contact:phone,
        depart_id: depart_id,
        role_id: role_id
      };
      console.log(body);
      let response = await axios.post(`${baseUrl}users/updateUser`, body,{headers})
      toast.success(response.data.message);
      // alert(response.data.message);
      console.log(response);
      navigate('/user');
      // alert(response.data.message);
      // navigate('/');
    } catch (e) {
      console.log(e);
    }
  }

  async function handleSubmit(e) {
    e.preventDefault();
    await updateUser(firstname, lastname, phone, superpartname, role);
    // db.collection("UserRole").where("userId","==", edituserID)
    // .get()
    // .then(doc=>{
    //  const userroleId = doc.docs[0].id
    //  db.collection("UserRole")
    //  .doc(userroleId)
    //  .update({
    //    Role: role,
    //    Partname: superpartname
    //  })
    //  .then(()=>{
    //    db.collection("Users")
    //    .doc(edituserID)
    //    .update({
    //      firstname:firstname,
    //      lastname: lastname,
    //      phone:phone,
    //    })
    //     .then(()=>{
    //      navigate('/user')
    //     })
    //   })
    // })
    // .catch((error) =>{
    //   console.log(error)
    // })
  }
  return (
    <div className="flex justify-center flex-col bg-indigo-50" style={{height:"90vh"}}>
      <div className="container max-w-3xl mx-auto bg-white rounded-xl shadow-xl overflow-hidden sm:max-w-xl pt-3  pb-3 flex justify-center items-center">
        <div className="px-2 py-1">
          <div className="uppercase text-3xl textstylecolor font-semibold text-center">Edit User</div>
          <hr/>
          <form className="w-full max-w-lg" onSubmit={handleSubmit}>
            <div className="flex flex-wrap -mx-3 mb-2">
              <div className="w-full md:w-1/2 px-3">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1 mb-1">
                  First Name
                </label>
                <input
                  type="text"
                  className="appearance-none block w-full text-gray-700 border border-gray-200 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                  placeholder="john"
                  value={firstname}
                  onChange={handlefirstName}
                  required={true}
                />
              </div>
              <div className="w-full md:w-1/2 px-3">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1 mb-1">
                  Last Name
                </label>
                <input
                  type="text"
                  className="appearance-none block w-full text-gray-700 border border-gray-200 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                  placeholder="Doe"
                  value={lastname}
                  onChange={handlelastName}
                  required={true}
                />
              </div>
            </div>
            <div className="flex flex-wrap -mx-3">
              <div className="w-full px-3 mb-1">
                <div className="flex justify-between">
                  <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                    Department
                  </label>
                </div>
                <select
                    className={"appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"}
                    // value={company}
                    name={"depart_id"}
                    value={userDepart}
                    onChange={handleCheck}
                >
                  <option selected>Select Department</option>
                  {depart.length > 0 ? (
                      depart.map((part ,id) => (
                          <option key={id} value={part.id}>{part.name} </option>
                      ))
                  ) : (
                      <option>No Department Found</option>
                  )
                  }
                </select>
                {/*<li className="flex flex-row items-center h-5 justify-center transform cursor-pointer text-gray-800">*/}
                {/*  <span className="uppercase ml-1 text-xl m-2 font-medium">Available Department List</span>*/}
                {/*</li>*/}
                {/*/!*<br/>*!/*/}
                {/*<ul*/}
                {/*    className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200 mt-2">*/}

                {/*  {depart.length > 0 ? (*/}
                {/*      depart.map((part, id) => (*/}
                {/*          <div key={id} className="flex justify-between mt-2 mb-2">*/}
                {/*            <label className="form-check-label inline-block text-gray-800" htmlFor="flexCheckDefault">{part.name}</label>*/}
                {/*            <input*/}
                {/*                type="radio"*/}
                {/*                className="form-checkbox h-4 w-4 text-blue-600"*/}
                {/*                value={part.id}*/}
                {/*                // checked={userDepart==part.id?true:false}*/}
                {/*                name={"depart_id"}*/}
                {/*                onChange={handleCheck}/>*/}
                {/*          </div>*/}
                {/*      ))*/}
                {/*  ) : (*/}
                {/*      <label className="form-check-label inline-block text-gray-800" htmlFor="flexCheckDefault">No Department</label>*/}
                {/*  )*/}
                {/*  }*/}
                {/*</ul>*/}
              </div>
            </div>
            <div className="flex flex-wrap -mx-3 mb-5">
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1 mb-1">
                  Role
                </label>
                <div className="relative">
                  <select
                      className={"appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 bordered"}
                      value={role}
                      style={{border:"1px solid #f0f0f0"}}
                      onChange={handleRole}
                      required={true}
                  >
                    <option disabled={true}>Select Role</option>
                    <option value={1}>Admin</option>
                    <option value={2}>Accountant</option>
                    <option value={3}>Department User</option>
                    <option value={4}>Management User</option>
                  </select>
                  <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                    <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" /></svg>
                  </div>
                </div>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1 mb-1">
                  Phone Number
                </label>
                <input
                  type="text"
                  className="appearance-none block w-full text-gray-700 border border-gray-200 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                  placeholder="+1 999-234-5678"
                  value={phone}
                  onChange={handlephone}
                  required={true}
                />
              </div>
            </div>
            <div className="flex flex-wrap -mx-3 mb-2">
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button type="submit" className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500">
                  <span className="mr-2 uppercase">Save</span>
                </button>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button type="button" className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" onClick={cancelButton}>
                  <span className="mr-2 uppercase">Cancel</span>
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}
