import React, { useEffect, useState } from 'react'
import '../../Components/assets/main.css'
import { Link,useNavigate } from 'react-router-dom'
import { db } from '../../firebase-config'
import { toast } from 'react-toastify';
import axios from "axios";
import {baseUrl} from "../../constants";

export default function Department() {
  const [departmentdata, setDepartmentData] = useState([]);
  const authID = sessionStorage.getItem('UID')
  const currentcominfo = sessionStorage.getItem('currentCompanyname')
  let navigate = useNavigate();
  async function getDeparts() {
    const token = localStorage.getItem('token');
    const headers = {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${token}`
    };
    await axios.get(`${baseUrl}users/getCompanyDeparts/${localStorage.getItem("companyID")}`, {headers})
        .then((res) => {

          let arr = [];
          for(let i = 0; i<res.data.data.length; i++) {
            console.log(res.data.data[i]);
            arr.push(res.data.data[i]);
          }
          setDepartmentData(arr)
        });
  }
  useEffect(async () => {
    if(localStorage.getItem(('companyID'))==null) {
      toast.info('Please activate a company to see records');
    }
    else {
      await getDeparts();
    }
  }, [])
  function onEdit(id) {
    console.log("Edit",id)
    navigate(`/editdepartment/${id}`);
  }

  async function onDelete(id) {
    console.log("delete", id);
    if (window.confirm('Are you sure to delete this department?')) {
      const token = localStorage.getItem('token');
      const headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      };
      await axios.get(`${baseUrl}users/deleteDepart/${id}`, {headers})
          .then((res) => {
            toast.info("Department Deleted Successfully");
          });

      await getDeparts();

    }
  }
  return (
    <div>
      <div className="min-w-screen flex justify-center px-5 py-5">
        <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-6">
          <div className="flex justify-between">
            <h2 className="text-2xl themeusercolor font-medium italic">Departments<h4 className={"font-medium"} style={{color:"#333",fontSize:"15px"}}>{departmentdata.length} Departments</h4></h2>
            <Link
                style={{height:"100%"}}
                to='/createdepartment'
                className="uppercase text-lg usecolor flex justify-between hover:bg-green-700 text-white py-3 px-2 rounded focus:outline-none focus:shadow-outline"
            >
              + Add Department
            </Link>
          </div>
          <div className="flex flex-col mt-2">
            <div className="py-2 -my-2 overflow-x-auto sm:-mx-6 sm:px-6 lg:-mx-6 lg:px-6">
              <div className="inline-block min-w-full overflow-hidden align-middle border border-gray-200 shadow sm:rounded-lg">
                <table className="min-w-full shadow-xl">
                  <thead className="bg-gray-500">
                    <tr>
                      <th
                        className="px-6 py-3 text-base font-medium leading-4 tracking-wider text-center text-gray-500 uppercase border-b border-gray-200 bg-gray-50">
                        No</th>
                      <th
                        className="px-6 py-3 text-base font-medium leading-4 tracking-wider text-center text-gray-500 uppercase border-b border-gray-200 bg-gray-50">
                        Department</th>
                      <th
                        className="px-6 py-3 text-base font-medium leading-4 tracking-wider text-center text-gray-500 uppercase border-b border-gray-200 bg-gray-50">
                        Sub_department</th>
                      <th
                        className="px-6 py-3 text-base font-medium leading-4 tracking-wider text-center text-gray-500 uppercase border-b border-gray-200 bg-gray-50">
                        Budget</th>
                      <th
                          className="px-6 py-3 text-base font-medium leading-4 tracking-wider text-center text-gray-500 uppercase border-b border-gray-200 bg-gray-50">
                        Account Name</th>
                      <th
                          className="px-6 py-3 text-base font-medium leading-4 tracking-wider text-center text-gray-500 uppercase border-b border-gray-200 bg-gray-50">
                        Account ID</th>
                      <th
                        className="px-6 py-3 text-base font-medium leading-4 tracking-wider text-center text-gray-500 uppercase border-b border-gray-200 bg-gray-50">
                        Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {departmentdata.length > 0 ? (
                      departmentdata.map((part, id) => (
                        <tr key={id} className="hover:bg-gray-100 border-b border-gray-200 py-10">
                          <td className="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
                            <div className="text-xl leading-5 text-gray-500 text-center">{id + 1}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
                            <div className="text-xl leading-5 text-gray-500 text-center">{part.parent_depart}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
                            <div className="text-xl leading-5 text-gray-500 text-center">{part.depart_name!=null?part.depart_name:"-"}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
                            <div className="text-xl leading-5 text-gray-500 text-center" type="number">${part.budget}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
                            <div className="text-xl leading-5 text-gray-500 text-center" type="number">{part.name}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-no-wrap border-b border-gray-200">
                            <div className="text-xl leading-5 text-gray-500 text-center" type="number">{part.accountID}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-no-wrap border-b border-gray-200 flex justify-between">
                            <svg xmlns="http://www.w3.org/2000/svg" className="w-6 h-6 textstylecolor hover:text-blue-600 cursor-pointer" fill="none"
                              viewBox="0 0 24 24" stroke="currentColor" onClick={() => onEdit(part.id)}
                            >
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"
                                d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z"

                              />
                            </svg>
                            <svg xmlns="http://www.w3.org/2000/svg" className="w-6 h-6 deletestylecolor hover:text-red-500 cursor-pointer" fill="none"
                              viewBox="0 0 24 24" stroke="currentColor" onClick={() => onDelete(part.id)}>
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"
                                d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                              />
                            </svg>
                          </td>
                        </tr>
                      ))
                    ) : (
                      <tr>
                        <td style={{textAlign:"center",padding:"20px"}} colSpan={7}>No Department Found</td>
                      </tr>
                    )
                    }
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}